import { Component, HostBinding, Input } from '@angular/core';

@Component({
  selector: 'mgl-timeline-entry-side',
  template: `
    <ng-content></ng-content>
  `,
  styles: [`
    :host {
      position: absolute;
      top: 0;
      left: 100%;
      width: 100%;
      text-align: center; }
      :host.alternate {
        left: -100%; }
      :host.mobile {
        display: none; }
  `]
})
export class MglTimelineEntrySideComponent {

  @HostBinding('class.alternate')
  alternate = false;

  @HostBinding('class.mobile')
  mobile = false;

}
